import React, {useState, useEffect, useRef} from 'react';
import {Paper, Container, Box, OutlinedInput, Typography, TextField, Select, Button, Divider} from '@material-ui/core'
import {FormControl, MenuItem, CircularProgress, DialogContent, Dialog} from '@material-ui/core'
import {post_data, get_data} from "../ApiCall";
import {useHistory} from 'react-router-dom'
import AssignmentRoundedIcon from '@material-ui/icons/AssignmentRounded';
import {InputAdornment} from '@material-ui/core'
import {makeStyles} from '@material-ui/core'
import CkEditorComponent from "../Component/CK_Editor/CkEditor.Component";
import {BACK_END_URL} from "../ServiceURL";
import DefaultImage from '../static/default-image.jpg'
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import Backdrop from '@material-ui/core/Backdrop';
import NumberFormat from 'react-number-format'
import ImageDialog from "../Component/ImageDialog";
import VisibilityIcon from '@material-ui/icons/Visibility';
import {IconButton} from "@material-ui/core";
import DialogPreview from "../Component/DialogPreview";

const useAddProductStyles = makeStyles(theme => ({
    container: {
        padding: "50px",
    },
    labelBtn: {
        display: "flex",
        flexDirection: "row",
        '&:hover': {
            cursor: "pointer"
        }
    },
    imgBtn: {},
    icon: {
        padding: '0.5rem',
        borderRadius: '50%',
        '&:hover': {
            backgroundColor: '#e8eaf6',
            cursor: 'pointer',
        }
    },
    backdrop: {
        zIndex: 999
    },
    price: {
        width: '90%',
        padding: '1rem',
        fontSize: '1rem'
    }
}));

const AddProductPage = () => {
    const classes = useAddProductStyles();
    const [state, setState] = useState({
        name: '',
        sub_category_id: 0,
        main_price: -1,
        sale_price: -1,
        remain : 1
    });

    const handleChange = (e) => {
        e.preventDefault()
        let name = e.target.name;
        let value = e.target.value;
        setState({
            ...state,
            [name]: value
        })
    }

    const history = useHistory();

    useEffect(() => {
        setOpenBackdrop(true)
        get_data(BACK_END_URL + '/public/category', {}, false)
            .then(res => {
                setListCat(res.data.list)
                setOpenBackdrop(false)
            })
            .catch(err => {
                setOpenBackdrop(false)
            });
    }, []);

    const [cat, setCat] = useState(0)
    const [listCat, setListCat] = useState([])
    const [subCat, setSubCat] = useState(0)
    const [listSubCat, setListSubCat] = useState([])

    const handleCatChange = (e) => {
        setCat(e.target.value)
        setSubCat(0)
    }
    useEffect(() => {
        if (cat !== 0) {
            setOpenBackdrop(true)
            get_data(BACK_END_URL + '/public/sub_category', {category_id: cat}, false)
                .then(res => {
                    setListSubCat(res.data.list)
                    setOpenBackdrop(false)
                })
                .catch(err => {
                    setOpenBackdrop(false)
                });
        } else {
            setListSubCat([])
        }
    }, [cat])
    const handleSubCatChange = (e) => {
        setSubCat(e.target.value)
    }

    const [dataSale, setDataSale] = useState('<i>Chương trình khuyến mãi...</i>')
    const [dataDescription, setDescription] = useState('<i>Chi tiết sản phẩm...</i>')
    const [dataSpec, setSpec] = useState('<i>Thông số kĩ thuật...</i>')
    const onSaleProgramChange = (event, editor) => {
        const data = editor.getData();
        setDataSale(data)
    }
    const onSpecChange = (event, editor) => {
        const data = editor.getData()
        setSpec(data)
    }
    const onDesChange = (event, editor) => {
        const data = editor.getData()
        setDescription(data)
    }

    const [image, setImage] = useState({
        url_image_main: null,
        url_image_1: null,
        url_image_2: null,
        url_image_3: null,
        url_image_4: null
    })

    const handleSave = () => {
        const data = {
            name: state.name,
            price: state.main_price,
            sale_price: state.sale_price,
            description: dataDescription,
            description_sale: dataSale,
            sub_category_id: subCat,
            specification: dataSpec,
            url_main_image: image.url_image_main,
            url_image_1: image.url_image_1,
            url_image_2: image.url_image_2,
            url_image_3: image.url_image_3,
            url_image_4: image.url_image_4,
            remain:state.remain,
            category_id : cat
        }
        if (data.name === '' | cat === 0) {
            alert('You need to fill form')
            return
        } else {
            post_data(BACK_END_URL + '/admin/product', {}, data, true)
                .then(res => {
                    alert('Save product successfully!!!!!!')
                    history.push('/products')
                })
        }
    }
    const [dataImage, setDataImage] = useState({
        name: null,
        src: null,
        aspect: 1/1,
    })
    const imageChange = (e, name) => {
        const file = e.target.files[0]
        let url = URL.createObjectURL(file)
        setDataImage({
            name: name,
            src: url,
            aspect: name == 'url_image_main' ? 1 / 1 : null
        })
        setOpenImage(true)
    }

    const handleDelImage = (e, name) => {
        setImage({
            ...image,
            [name]: null
        })
    }
    const [openBackdrop, setOpenBackdrop] = useState(false)
    const handlePriceMain = ((values) => {
        const {formattedValue, value} = values;
        setState({
            ...state,
            main_price: value
        })
    })
    const handlePriceSale = ((values) => {
        const {formattedValue, value} = values;
        setState({
            ...state,
            sale_price: value
        })
    })
    const handleSetImage = (e => {
        let name = e.name
        let value = e.url_image
        setImage({
            ...image,
            [name]: value
        })
    })
    const handleImageClose = () => {
        setOpenImage(false)
    }
    const [openImage, setOpenImage] = useState(false)
    const [openPreview, setOpenPreview] = useState(false)
    return (
        <Box style={{padding: "30px"}}>
            <Typography style={{fontSize: "25px", fontWeight: "bold", marginBottom: "10px"}}>New
                Products</Typography>
            <Box style={{display: "flex", flexDirection: "row"}}>
                <Box style={{width: "100%", minWidth: "750px"}}>
                    <Paper style={{padding: "30px", display: "flex", flexDirection: "column"}}>
                        <Typography style={{marginBottom: "5px"}}>Title</Typography>
                        <OutlinedInput placeholder="Title *" type="text" name="name" value={state.name}
                                       onChange={handleChange} required
                                       endAdornment={
                                           <InputAdornment position="end">
                                               <AssignmentRoundedIcon/>
                                           </InputAdornment>}
                        />
                        <div style={{display: 'flex', flexDirection: 'row'}}>
                            <div style={{width: '40%', margin: "20px 20px 20px 0"}}>
                                <div style={{position: 'relative', width: '100%', zIndex: 0, height: '300px'}}>
                                    <div style={{
                                        display: 'flex', flexDirection: 'row',
                                        position: 'absolute', bottom: 0, zIndex: 1,
                                        left: '50%', transform: 'translateX(-50%)', marginBottom: '1rem'
                                    }}>
                                        <div>
                                            <label htmlFor={'main_image'}>
                                                <EditIcon className={classes.icon}/>
                                            </label>
                                            <input id='main_image' type='file' name='file'
                                                   onChange={e => imageChange(e, 'url_image_main')}
                                                   hidden={true}/>
                                        </div>
                                        <div style={{marginLeft: '1rem'}}>
                                            <DeleteIcon className={classes.icon}
                                                        onClick={e => handleDelImage(e, 'url_image_main')}/>
                                        </div>
                                    </div>
                                    <img alt='main-img'
                                         style={{width: '100%', zIndex: 0, height: '100%', objectFit: 'contain'}}
                                         src={image.url_image_main != null ? image.url_image_main :
                                             DefaultImage}/>
                                </div>
                                <Divider/>
                                <div style={{
                                    display: 'flex',
                                    flexDirection: 'row',
                                    justifyContent: 'space-between'
                                }}>
                                    <div style={{position: 'relative', width: '25%', height: '130px'}}>
                                        <div style={{
                                            display: 'flex', flexDirection: 'row',
                                            position: 'absolute', bottom: 0, zIndex: 1,
                                            left: '50%', transform: 'translateX(-50%)'
                                        }}>
                                            <div>
                                                <label htmlFor={'image_1'}>
                                                    <EditIcon className={classes.icon}/>
                                                </label>
                                                <input id='image_1' type='file' name='file'
                                                       onChange={e => imageChange(e, 'url_image_1')}
                                                       hidden={true}/>
                                            </div>
                                            <div style={{marginLeft: '1rem'}}>
                                                <DeleteIcon className={classes.icon}
                                                            onClick={e => handleDelImage(e, 'url_image_1')}/>
                                            </div>
                                        </div>
                                        <img alt='main-img'
                                             style={{width: '100%', height: '130px', objectFit: 'contain'}}
                                             src={image.url_image_1 != null ? image.url_image_1 :
                                                 DefaultImage}/>
                                    </div>
                                    <Divider orientation={'vertical'} flexItem={true}/>
                                    <div style={{position: 'relative', width: '25%', height: '130px'}}>
                                        <div style={{
                                            display: 'flex', flexDirection: 'row',
                                            position: 'absolute', bottom: 0, zIndex: 1,
                                            left: '50%', transform: 'translateX(-50%)'
                                        }}>
                                            <div>
                                                <label htmlFor={'image_2'}>
                                                    <EditIcon className={classes.icon}/>
                                                </label>
                                                <input id='image_2' type='file' name='file'
                                                       onChange={e => imageChange(e, 'url_image_2')}
                                                       hidden={true}/>
                                            </div>
                                            <div style={{marginLeft: '1rem'}}>
                                                <DeleteIcon className={classes.icon}
                                                            onClick={e => handleDelImage(e, 'url_image_2')}/>
                                            </div>
                                        </div>
                                        <img alt='main-img'
                                             style={{width: '100%', height: '130px', objectFit: 'contain'}}
                                             src={image.url_image_2 != null ? image.url_image_2 :
                                                 DefaultImage}/>
                                    </div>
                                    <Divider orientation={'vertical'} flexItem={true}/>
                                    <div style={{position: 'relative', width: '25%', height: '130px'}}>
                                        <div style={{
                                            display: 'flex', flexDirection: 'row',
                                            position: 'absolute', bottom: 0, zIndex: 1,
                                            left: '50%', transform: 'translateX(-50%)'
                                        }}>
                                            <div>
                                                <label htmlFor={'image_3'}>
                                                    <EditIcon className={classes.icon}/>
                                                </label>
                                                <input id='image_3' type='file' name='file'
                                                       onChange={e => imageChange(e, 'url_image_3')}
                                                       hidden={true}/>
                                            </div>
                                            <div style={{marginLeft: '1rem'}}>
                                                <DeleteIcon className={classes.icon}
                                                            onClick={e => handleDelImage(e, 'url_image_3')}/>
                                            </div>
                                        </div>
                                        <img alt='main-img'
                                             style={{width: '100%', height: '130px', objectFit: 'contain'}}
                                             src={image.url_image_3 != null ? image.url_image_3 :
                                                 DefaultImage}/>
                                    </div>
                                    <Divider orientation={'vertical'} flexItem={true}/>
                                    <div style={{position: 'relative', width: '25%', height: '130px'}}>
                                        <div style={{
                                            display: 'flex', flexDirection: 'row',
                                            position: 'absolute', bottom: 0, zIndex: 1,
                                            left: '50%', transform: 'translateX(-50%)'
                                        }}>
                                            <div>
                                                <label htmlFor={'image_4'}>
                                                    <EditIcon className={classes.icon}/>
                                                </label>
                                                <input id='image_4' type='file' name='file'
                                                       onChange={e => imageChange(e, 'url_image_4')}
                                                       hidden={true}/>
                                            </div>
                                            <div style={{marginLeft: '1rem'}}>
                                                <DeleteIcon className={classes.icon}
                                                            onClick={e => handleDelImage(e, 'url_image_4')}/>
                                            </div>
                                        </div>
                                        <img alt='main-img'
                                             style={{width: '100%', height: '130px', objectFit: 'contain'}}
                                             src={image.url_image_4 != null ? image.url_image_4 :
                                                 DefaultImage}/>
                                    </div>
                                </div>
                            </div>
                            <div style={{display: 'flex', flexDirection: 'column', width: '60%'}}>
                                <div style={{
                                    display: "flex",
                                    flexDirection: "row",
                                    justifyContent: "space-between"
                                }}>
                                    <div style={{width: "48%"}}>
                                        <Typography style={{margin: "20px 0 10px 0", fontSize: "14px"}}>Category
                                            :</Typography>
                                        <FormControl variant="outlined" required style={{width: "100%"}}>
                                            <Select
                                                value={cat}
                                                onChange={handleCatChange}
                                            >
                                                <MenuItem value={0} disabled>Select Category</MenuItem>
                                                {
                                                    listCat.map((row) => <MenuItem
                                                        value={row.id} key={row.id}>{row.name}</MenuItem>)
                                                }
                                            </Select>
                                        </FormControl>
                                    </div>
                                    <div style={{width: "48%"}}>
                                        <Typography style={{margin: "20px 0 10px 0", fontSize: "14px"}}>SubCategory
                                            :</Typography>
                                        <FormControl variant="outlined" required style={{width: "100%"}}>
                                            <Select
                                                value={subCat}
                                                onChange={handleSubCatChange}
                                            >
                                                <MenuItem value={0} disabled>Select sub_category</MenuItem>
                                                {
                                                    listSubCat.map((row) => <MenuItem
                                                        value={row.id} key={row.id}>{row.name}</MenuItem>)
                                                }
                                            </Select>
                                        </FormControl>
                                    </div>
                                </div>
                                <div style={{
                                    display: "flex",
                                    flexDirection: "row",
                                    justifyContent: "space-between"
                                }}>
                                    <div style={{width: "32%", display: 'flex', flexDirection: 'column'}}>
                                        <Typography style={{margin: "20px 0 10px 0", fontSize: "14px"}}>Main
                                            price
                                            :</Typography>
                                        <NumberFormat thousandSeparator={true} value={state.main_price}
                                                      className={classes.price}
                                                      placeholder="Main Price *"
                                                      onValueChange={handlePriceMain} required/>
                                    </div>
                                    <div style={{width: "32%"}}>
                                        <Typography style={{margin: "20px 0 10px 0", fontSize: "14px"}}>Sale
                                            price
                                            :</Typography>
                                        <NumberFormat thousandSeparator={true} value={state.sale_price}
                                                      placeholder="Sale Price *"
                                                      className={classes.price}
                                                      onValueChange={handlePriceSale} required/>
                                    </div>
                                    <div style={{width: "32%"}}>
                                        <Typography style={{margin: "20px 0 10px 0", fontSize: "14px"}}>Remain
                                            :</Typography>
                                        <OutlinedInput type={'number'} name = 'remain' value={state.remain} onChange={handleChange}
                                        style={{width:'100%'}}/>
                                    </div>
                                </div>
                                <div style={{marginTop: '1rem', minHeight: '20rem'}} className={'sale-program'}>
                                    <CkEditorComponent onChange={onSaleProgramChange} data={dataSale}/>
                                </div>
                            </div>
                        </div>
                    </Paper>

                    <Paper
                        style={{padding: "1rem", marginTop: "2rem", display: "flex", flexDirection: "column"}}>
                        <div style={{display: 'flex', flexDirection: 'row'}}>
                            <div style={{width: '70%', margin: "0px 20px 20px 0"}}>
                                <Button onClick={()=>{setOpenPreview(true)}}>
                                    <VisibilityIcon/>{' Preview'}
                                </Button>
                                <CkEditorComponent data={dataDescription} onChange={onDesChange}/>
                            </div>
                            <div style={{width: '30%', margin: "35px 20px 20px 0"}}>
                                <CkEditorComponent data={dataSpec} onChange={onSpecChange}/>
                            </div>
                        </div>
                    </Paper>
                    <Divider style={{marginTop: "30px"}}/>
                    <div style={{
                        display: "flex",
                        flexDirection: "row",
                        justifyContent: "flex-end",
                        marginTop: "10px"
                    }}>
                        <Button variant="contained" color="primary" size="large" type="submit" onClick={handleSave}>Save
                            Product</Button>
                        <Button variant="contained" size="large"
                                style={{backgroundColor: "white", marginLeft: "10px"}}
                        >Cancel</Button>
                    </div>
                </Box>
            </Box>
            <Backdrop className={classes.backdrop} open={openBackdrop}>
                <CircularProgress color="inherit"/>
            </Backdrop>
            <ImageDialog data={dataImage} open={openImage} setPImage={handleSetImage} onClose={handleImageClose}/>
            <DialogPreview open = {openPreview} data = {dataDescription} onClose={()=>setOpenPreview(false)}/>
        </Box>
    );
};

export default AddProductPage;